
.Room-Wrapper{

    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    min-height: 100vh;
    background-image: url('../Images/sky.jpg');
    background-position: cover;
}

.Dashboard-Wrapper{
    display: flex;
    justify-content: flex-end;

    }
 .Dashboard-Model{
        border: none;
        max-width: 17%;
    }
  @keyframes neon-pulse {
    from {
      border-color: #00ff00;
      box-shadow: 0 0 15px #00ff00;
    }
    to {
      border-color: #33ff33;
      box-shadow: 0 0 25px #33ff33;
    }
  }
.Dashboard{
    display: flex;
    width: 100%;
    justify-content: center;
    border: 10px solid #00ff00; 
    animation: neon-pulse 2s ease-in-out infinite alternate;
}


.Dashboard-Tools{
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    border-right: 10px solid #00ff00; 
    padding-top:5px;
    height: 100%;
    justify-content: space-evenly;
    animation: neon-pulse 2s ease-in-out infinite alternate;
}
.Dashboard-Tools button{
    background-color: transparent;
    padding:10px;  
    line-height: 1;
    border:none ;
    font-family: "Press Start 2P",sans-serif;
    cursor: pointer;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: bold;
    color: #00c800;
    border: 2px solid white;
    
}
.Join-Room{
    display: flex;
    justify-content: center;
    line-height: 0;
    width: 100%;
    height: 10%;

}
.Join-Room .Input-Wrapper{
    display: flex;
    width: 100%;
    padding-left: 5px;;
    
}
.Input-Wrapper input{
    font-size: 13px;
    font-family: "Press Start 2P",sans-serif;
    text-align: center;
    width: 100%;
}
.Input-Wrapper input::placeholder{
        color: #00c800;
    }

.Join-Room .Buttons-Wrapper{
    display: flex;
    gap: 3px;
    width:50%;
    padding-right: 5px;
    
}
.Join-Room .Buttons-Wrapper button{
    background-color: transparent;
    line-height: 2;
    font-family: "Press Start 2P",sans-serif;
    cursor: pointer;
    font-size:10px;
    text-transform: uppercase;
    font-weight: bold;
    border: 2px solid white;
    text-align: center;
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;

}
.Join-Room #addRoomButton{
    font-family: "Press Start 2P",sans-serif;
    font-weight: bolder;
    font-size: 14px;
    width: 30%;
    justify-content: center;
    align-items: center;
 display: flex;
}
.NavButtons-Wrapper{
    display: flex;
    width: 100%;
    gap: 7px;
    justify-content: center;
    align-items: center;
}
.NavButtons-Wrapper button{
    border: 6px solid black;
    width: 21%;
    border-radius: 70%;
    padding: 0;
    position: relative;
}
.NavButtons-Wrapper button:hover{transform: scale(1.05);}
.NavButtons-Wrapper span{
    color: white!important;
    font-weight: bold   ;
    font-size: 8px;
}
.NavButtons-Wrapper button img{
    height: 100%;
    width: 100%;
}